import React from 'react';
import ReactDOM from 'react-dom';
import * as serviceWorker from './serviceWorker';
import { CSSTransition } from 'react-transition-group';
import './scss/common.scss';

// VIEWS
import MainView from './views/MainView';
import MainViewNew from './views/MainViewNew';
import NotFoundView from './views/NotFoundView';
import LoginView from './views/LoginView';
import UserProfileView from './views/UserProfileView';
import ClubListView from './views/ClubListView';
import ClubListViewNew from './views/ClubListViewNew';
import ClubDetailView from './views/ClubDetailView';
import QnaListView from './views/QnaListView';
import QnaListViewNew from './views/QnaListViewNew';
import QnaDetailView from './views/QnaDetailView';
import PostListView from './views/PostListView';
import PostDetailView from './views/PostDetailView';
import PostInClubView from './views/PostInClubView';

import { Navbar, AxiosProgressBar } from './components';

// ROUTER
import { Router, Route, Switch, useHistory } from 'react-router-dom';

// REDUX
import { createStore, applyMiddleware } from 'redux';
import { Provider } from 'react-redux';
// import Thunk from 'redux-thunk';
// import logger from 'redux-logger';
import { composeWithDevTools } from 'redux-devtools-extension';
import rootReducer, { rootSaga } from './modules';
import { createBrowserHistory } from 'history';
import createSagaMiddleware from 'redux-saga';

// const store = createStore(rootReducer, applyMiddleware(Thunk));

const customHistory = createBrowserHistory();
const sagaMiddleware = createSagaMiddleware({
    context: {
        history: customHistory,
    },
}); //   .

export const store = createStore(
    rootReducer,
    // logger   , logger   .
    composeWithDevTools(
        applyMiddleware(
            // ReduxThunk.withExtraArgument({ history: customHistory }),
            sagaMiddleware, //   
            // logger,
        ),
    ),
); //      .

sagaMiddleware.run(rootSaga); //   .
// :       .

ReactDOM.render(
    <React.StrictMode>
        <Provider store={store}>
            <Router history={customHistory}>
                <>
                    <AxiosProgressBar store={store} />
                    <Navbar />
                    <CSSTransition timeout={3000000} className="fade">
                        <Switch>
                            <Route exact path="/" component={MainViewNew}></Route>
                            <Route path="/login" component={LoginView}></Route>
                            <Route path="/profile" component={UserProfileView}></Route>
                            <Route exact path="/club" component={ClubListViewNew}></Route>
                            <Route
                                exact
                                path="/club/:club_id/qna/:qna_id"
                                component={QnaDetailView}
                            ></Route>
                            <Route
                                exact
                                path="/club/:club_id/qna"
                                component={QnaListViewNew}
                            ></Route>
                            <Route exact path="/club/:club_id" component={ClubDetailView}></Route>
                            <Route
                                exact
                                path="/club/:club_id/post"
                                component={PostInClubView}
                            ></Route>
                            <Route exact path="/post" component={PostListView}></Route>
                            <Route
                                exact
                                path="/club/:club_id/post/:post_id"
                                component={PostDetailView}
                            ></Route>
                            <Route path="*" component={NotFoundView}></Route>
                        </Switch>
                    </CSSTransition>
                </>
            </Router>
        </Provider>
    </React.StrictMode>,
    document.getElementById('root'),
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
